<template>
  <v-toolbar
    id="core-toolbar"
    app
    flat
    prominent
    style="background: #eee;"
  >
    <div class="v-toolbar-title">
      <v-toolbar-title
        class="tertiary--text font-weight-light"
      >
        <v-btn
          v-if="responsive"
          class="default v-btn--simple"
          dark
          icon
          @click.stop="onClickBtn"
        >
          <v-icon>mdi-view-list</v-icon>
        </v-btn>
        <span class="headline"> {{ title }}</span>
      </v-toolbar-title>
    </div>

    <v-spacer />
    <v-toolbar-items>
      <v-flex
        align-center
        layout
        py-2
      >
        <template v-if="roles().length > 1">
          <v-select
            :items="roles()"
            v-model="roleAtiva"
            class="caption text-lowercase"
            @change="changeRoleAtiva"/>
        </template>
        <div
          style="cursor: pointer"
          class="mt-3 layout align-center justify-center row fill-height"
          @click="onNotificacoes()"
        >
          <v-badge
            color="error"
            overlap
          >
            <template
              v-if="notificacoes.length"
              slot="badge">
              {{ notificacoes.length }}
            </template>
            <v-icon>mdi-bell</v-icon>
          </v-badge>
        </div>
        <v-menu
          bottom
          left
          content-class="dropdown-menu"
          offset-y
          transition="slide-y-transition">
          <template
            slot="activator"
          >
            <v-avatar
              class="ml-4 white--text primary font-weight-medium"
            >
              {{ dadosUsuario.nome.substr(0, 2).toUpperCase() }}
            </v-avatar>
          </template>
          <v-card>
            <v-card-text>
              <div class="text-xs-center">
                <v-avatar
                  class="mb-2 white--text primary font-weight-medium"
                >
                  {{ dadosUsuario.nome.substr(0, 2).toUpperCase() }}
                </v-avatar><br>
                <span class="title">{{ dadosUsuario.nome }}</span><br>
                <span class="caption">{{ dadosUsuario.email }}</span><br>
                <div>
                  <span class="caption">Login: </span><span class="body-1">{{ dadosUsuario.login }}</span><br>
                  <span class="caption">Perfil Ativo: </span><span class="body-1">{{ $root.roleAtiva() }} </span><br>
                  <template
                    v-if="$root.roleAtiva().toUpperCase() === 'CREDIARISTA'"
                  >
                    <span class="caption">Loja: </span><span class="body-1">{{ dadosUsuario.lojasLabel }} </span>
                  </template>
                </div>
              </div>
            </v-card-text>
            <v-divider/>
            <v-card-text>
              <v-list-tile
                v-for="item in itensMenu"
                :key="item.name"
                class="v-list-item"
                @click="onClickMenuItem(item)"
              >
                <v-list-tile-action>
                  <v-icon>{{ item.icon }}</v-icon>
                </v-list-tile-action>
                <v-list-tile-title
                  v-text="item.name"
                />
              </v-list-tile>
            </v-card-text>
          </v-card>
        </v-menu>
      </v-flex>
    </v-toolbar-items>
  </v-toolbar>
</template>

<script>

import {
  mapMutations
} from 'vuex'
import { NotificacaoBusiness, UsuarioBusiness } from '../../business'
import EventBus from '../../utils/eventBus'

export default {
  data: () => ({
    notificacoes: [],
    title: null,
    responsive: false,
    dadosUsuario: null,
    itensMenu: [],
    roleAtiva: null
  }),

  watch: {
    '$route' (val) {
      this.title = val.name
    }
  },

  mounted () {
    this.onResponsiveInverted()
    window.addEventListener('resize', this.onResponsiveInverted)
    EventBus.$on('NOTIFICACAO_VISUALIZADA', (id) => {
      this.notificacoes = this.notificacoes.filter(item => item.id !== id)
    })

    EventBus.$on('NOVAS_NOTIFICACOES', () => {
      this.loadNotificacoes()
    })
  },
  beforeDestroy () {
    window.removeEventListener('resize', this.onResponsiveInverted)
  },
  beforeMount () {
    this.title = this.$route.name
    this.roleAtiva = this.$root.roleAtiva()
    this.dadosUsuario = this.$root.dadosUsuario()
    this.populaItensMenu()

    if (!this.$root.lojaId()) {
      return
    }
    this.loadNotificacoes()
  },

  methods: {
    ...mapMutations('app', ['setDrawer', 'toggleDrawer']),
    onClickBtn () {
      this.setDrawer(!this.$store.state.app.drawer)
    },
    populaItensMenu () {
      if (this.$root.roleAtiva().toUpperCase() === 'CREDIARISTA') {
        this.itensMenu.push({ name: 'Início', icon: 'mdi-home', to: '/home' })
      } else {
        this.itensMenu.push({ name: 'Dashboard', icon: 'mdi-view-dashboard', to: '/' })
      }
      this.itensMenu.push({ name: 'Sair', icon: 'mdi-logout', to: '' })
    },
    onNotificacoes (id) {
      if (id) {
        this.$router.push(`/notificacoes/${id}`)
      } else {
        this.$router.push('/notificacoes')
      }
    },
    onClickMenuItem (item) {
      if (item.name === 'Sair') {
        this.logout()
      }
      if (item.name === 'Dashboard') {
        this.$router.push('/')
      }
      if (item.name === 'Início') {
        this.$router.push(item.to)
      }
      if (item.name === 'Configuração') {
        this.$router.push('/config')
      }
    },
    onResponsiveInverted () {
      if (window.innerWidth < 991) {
        this.responsive = true
      } else {
        this.responsive = false
      }
    },
    logout () {
      localStorage.clear()
      window.location.reload()
    },
    loadNotificacoes () {
      let filtroNotificacao = {
        lojaId: this.$root.lojaId(),
        visualizado: false,
        prioridade: null,
        tag: ''
      }
      NotificacaoBusiness.paginationLoja(filtroNotificacao)
        .then(response => {
          this.notificacoes = response.data.content
          if (this.notificacoes.length > 0) {
            this.$root.showInfo('Você possui novas notificações')
          }
        })
    },
    roles () {
      return this.$root.roles()
    },
    changeRoleAtiva (item) {
      this.roleAtiva = item
      UsuarioBusiness.updateRoleAtiva(this.$root.usuarioId(), this.roleAtiva)
        .then(response => {
          this.$root.setRoleAtiva(this.roleAtiva)
          window.location.reload()
        })
        .catch(() => this.$root.showErro('Erro ao alterar o perfil de acesso do usuário'))
    }
  }
}
</script>

<style>
  #core-toolbar a {
    text-decoration: none;
  }
</style>
