<template>
  <v-navigation-drawer
    id="app-drawer"
    v-model="inputValue"
    app
    dark
    floating
    mobile-break-point="991"
    width="270"
    class="nav-local"
  >
    <v-layout
      class="fill-height v-background-menu"
      tag="v-list"
      column>
      <v-card
        class="white card-logo"
      >
        <v-card-text>
          <v-layout
            align-center
            justify-center
            row
            fill-height>
            <a
              href="/">
              <v-img
                src="/img/logo-menu.jpeg"
                height="50px"
                width="162px"
              />
            </a>
          </v-layout>
        </v-card-text>
      </v-card>
      <v-list>
        <div
          v-for="(link, i) in acoesPermitidas(links)"
          :key="i"
        >
          <v-list-tile
            v-if="link.children.length === 0"
            :to="link.to"
            active-class="primary"
            avatar
            class="v-list-item"
          >
            <v-list-tile-action>
              <v-icon>{{ link.icon }}</v-icon>
            </v-list-tile-action>
            <v-list-tile-title v-text="link.text"/>
          </v-list-tile>

          <v-list-group
            v-else
            :prepend-icon="link.icon"
          >
            <template :slot="'activator'">
              <v-list-tile
                active-class="primary"
                class="v-list-item"
              >
                <v-list-tile-title v-text="link.text" />
              </v-list-tile>
            </template>

            <v-list-tile
              v-for="(child, j) in acoesPermitidas(link.children)"
              :key="j"
              :to="child.to"
              active-class="primary"
              avatar
              class="v-list-item"
            >
              <v-list-tile-title v-text="child.text" />
              <v-list-tile-action>
                <v-icon>{{ child.icon }}</v-icon>
              </v-list-tile-action>
            </v-list-tile>
          </v-list-group>
        </div>
        <v-list-tile
          :key="'altearSenha'"
          active-class="primary"
          avatar
          class="v-list-item"
          @click="openAlterarSenha"
        >
          <v-list-tile-action>
            <v-icon>mdi-lock-reset</v-icon>
          </v-list-tile-action>
          <v-list-tile-title
            v-text="'Alterar Senha'"
          />
        </v-list-tile>
      </v-list>
    </v-layout>
    <core-alterar-senha
      v-if="dialogAlterarSenha"
      :usuario-id="$root.usuarioId()"
      :show="dialogAlterarSenha"
      :close="closeAlterarSenha"
    />
  </v-navigation-drawer>
</template>

<script>
import { mapMutations, mapState } from 'vuex'

import { LOCALSTORAGE } from '../../constants'
import { UsuarioBusiness } from '../../business'

// roles: ADMIN, SUPERVISOR, COMERCIAL, NEGOCIADOR, PROPRIETARIO, CREDIARISTA
export default {
  props: {
    opened: {
      type: Boolean,
      default: false
    }
  },
  data: () => ({
    roleAtiva: null,
    dialogAlterarSenha: false,
    logo: '/favicon.ico',
    links: [
      {
        to: '/',
        icon: 'mdi-view-dashboard',
        text: 'Dashboard',
        roles: ['ADMIN', 'SUPERVISOR', 'COMERCIAL', 'NEGOCIADOR', 'PROPRIETARIO'],
        children: []
      },
      {
        to: '/home',
        icon: 'mdi-home',
        text: 'Início',
        roles: ['CREDIARISTA'],
        children: []
      },
      {
        to: '/lista-user',
        icon: 'mdi-account',
        text: 'Usuários',
        roles: ['ADMIN'],
        children: []
      },
      {
        to: '/perfis-acesso',
        icon: 'mdi-account-cog',
        text: 'Perfis de Acesso',
        roles: ['ADMIN', 'SUPERVISOR'],
        children: []
      },
      {
        to: '/lista-rede',
        icon: 'mdi-wan',
        text: 'Redes',
        roles: ['ADMIN', 'SUPERVISOR'],
        children: []
      },
      {
        to: '/lista-empresa',
        icon: 'mdi-domain',
        text: 'Empresas',
        roles: ['ADMIN', 'SUPERVISOR'],
        children: []
      },
      {
        to: '/lista-loja',
        icon: 'mdi-store',
        text: 'Lojas',
        roles: ['ADMIN', 'SUPERVISOR'],
        children: []
      },
      {
        to: '/vender',
        icon: 'mdi-cart',
        text: 'Vender',
        roles: ['ADMIN', 'CREDIARISTA', 'PROPRIETARIO'],
        children: []
      },
      {
        to: '/lista-cliente',
        icon: 'mdi-card-account-details',
        text: 'Clientes',
        roles: ['ADMIN', 'SUPERVISOR', 'COMERCIAL', 'NEGOCIADOR', 'PROPRIETARIO', 'CREDIARISTA'],
        children: []
      },
      {
        to: '/lista-planopagamento',
        icon: 'mdi-cash-multiple',
        text: 'Planos de Pagamento',
        roles: ['ADMIN', 'SUPERVISOR'],
        children: []
      },
      {
        to: '/pagarEmLoja',
        icon: 'mdi-cash-multiple',
        text: 'Pagar',
        roles: ['PROPRIETARIO', 'CREDIARISTA'],
        children: []
      },
      {
        to: '/pagamentos',
        icon: 'mdi-cash-multiple',
        text: 'Pagamentos',
        roles: ['ADMIN', 'SUPERVISOR', 'COMERCIAL', 'NEGOCIADOR'],
        children: []
      },
      {
        to: '/autorizacao',
        icon: 'mdi-alpha-a-box-outline',
        text: 'Autorizações',
        roles: ['ADMIN', 'SUPERVISOR', 'COMERCIAL', 'NEGOCIADOR', 'PROPRIETARIO', 'CREDIARISTA'],
        children: []
      },
      {
        to: '/cobranca',
        icon: 'mdi-headset',
        text: 'Cobrança',
        roles: ['ADMIN', 'SUPERVISOR', 'COMERCIAL', 'NEGOCIADOR'],
        children: []
      },
      {
        to: '/notificacoes',
        icon: 'mdi-bell',
        text: 'Notificações',
        roles: ['ADMIN', 'SUPERVISOR', 'COMERCIAL', 'NEGOCIADOR', 'PROPRIETARIO', 'CREDIARISTA'],
        children: []
      },
      {
        to: '/spc',
        icon: 'mdi-account-off-outline',
        text: 'SPC',
        roles: ['ADMIN', 'SUPERVISOR', 'COMERCIAL', 'NEGOCIADOR'],
        children: []
      },
      {
        to: '/relatorios',
        icon: 'mdi-file-document',
        text: 'Relatórios',
        roles: ['ADMIN', 'PROPRIETARIO'],
        children: [
          /*
          {
            to: '/rel-repasse-lancamento-extra',
            text: 'Repasse Lançamento Extra',
            roles: ['ADMIN', 'PROPRIETARIO']
          }, */
          {
            to: '/rel-clientes',
            text: 'Relação de Clientes',
            roles: ['ADMIN', 'PROPRIETARIO']
          },
          {
            to: '/mov-compras',
            text: 'Movimento de Compras',
            roles: ['ADMIN', 'PROPRIETARIO']
          },
          {
            to: '/pag-clientes',
            text: 'Pagamento de Clientes',
            roles: ['ADMIN', 'PROPRIETARIO']
          },
          {
            to: '/rel-repasse-pagamento-efetuado',
            text: 'Repasse Pagto Efetuado',
            roles: ['ADMIN', 'PROPRIETARIO']
          },
          {
            to: '/rel-repasse-previsao-pagamento',
            text: 'Repasse Previsão de Pagto',
            roles: ['ADMIN', 'PROPRIETARIO']
          }
        ]
      },
      {
        to: '',
        icon: 'mdi-bank-transfer',
        text: 'Repasse',
        roles: ['ADMIN'],
        children: [
          {
            to: '/repasse-lancamento-extra',
            icon: '',
            text: 'Lançamento Extra',
            roles: ['ADMIN']
          },
          {
            to: '/repasse-calculo-individual',
            icon: '',
            text: 'Cálculo Individual',
            roles: ['ADMIN']
          },
          {
            to: '/repasse-calculo-geral',
            icon: '',
            text: 'Cálculo Geral',
            roles: ['ADMIN']
          },
          {
            to: '/reembolso-repasse',
            icon: '',
            text: 'Reembolsar Repasse',
            roles: ['ADMIN']
          }
        ]
      },
      {
        to: '/rps',
        icon: 'mdi-file',
        text: 'RPS',
        roles: ['ADMIN'],
        children: []
      },
      {
        to: '/configuracao',
        icon: 'mdi-cog',
        text: 'Configurações',
        roles: ['ADMIN'],
        children: []
      },
      {
        to: '/auditoria',
        icon: 'mdi-database-search',
        text: 'Auditoria',
        roles: ['ADMIN'],
        children: []
      }
    ]
  }),
  computed: {
    ...mapState('app', ['image', 'color']),
    inputValue: {
      get () {
        return this.$store.state.app.drawer
      },
      set (val) {
        this.setDrawer(val)
      }
    },
    items () {
      return this.$t('Layout.View.items')
    }
  },

  beforeMount () {
    this.roleAtiva = this.$root.roleAtiva()
  },

  methods: {
    ...mapMutations('app', ['setDrawer', 'toggleDrawer']),
    username () {
      return localStorage.getItem(LOCALSTORAGE.LOGIN)
    },
    userProfile () {
      this.$router.push('user-profile')
    },
    roles () {
      return this.$root.roles()
    },
    changeRoleAtiva (item) {
      this.roleAtiva = item
      UsuarioBusiness.updateRoleAtiva(this.$root.usuarioId(), this.roleAtiva)
        .then(response => {
          this.$root.setRoleAtiva(this.roleAtiva)
          document.location.reload()
        })
        .catch(() => this.$root.showErro('Erro ao alterar o perfil de acesso do usuário'))
    },
    acoesPermitidas (lista) {
      return lista.filter(a => a.roles.includes(this.$root.roleAtiva().toUpperCase()))
    },
    openAlterarSenha () {
      this.dialogAlterarSenha = true
    },
    closeAlterarSenha () {
      this.dialogAlterarSenha = false
    }
  }
}
</script>

<style lang="scss">
.nav-local {
  background-color: rgba(27, 27, 27, 1) !important;
}

#app-drawer {
  .v-list__tile {
    border-radius: 4px;

    &--buy {
      margin-top: auto;
      margin-bottom: 17px;
    }
  }
  .logo-pratico {
    padding: 6px;
    background-color:white;
  }
  .card-logo {
    border-radius: 0px !important;
  }
}
</style>
