<template>
  <tr>
    <td
      v-if="!!item.isVigente"
      class="text-xs-center">
      <v-icon color="success">mdi-check-bold</v-icon>
    </td>
    <td v-else />
    <td
      v-if="!!item.dataVigencia"
      class="text-xs-center"
    >{{ item.dataVigencia | moment("DD/MM/YYYY") }}</td>
    <td v-else>{{ '-' }}</td>
    <td
      class="text-xs-right"
    >{{ item.peMultaPorAtraso.toLocaleString("pt-BR", { minimumFractionDigits: 2 }) + '%' }}</td>
    <td
      class="text-xs-right"
    >{{ item.peJurosMoraPorAtraso.toLocaleString("pt-BR", { minimumFractionDigits: 2 }) + '%' }}</td>
    <td
      class="text-xs-right"
    >{{ 'R$ ' + item.vlTarifaParcelaBanco.toLocaleString("pt-BR", { minimumFractionDigits: 2 }) }}</td>
    <td
      class="text-xs-right"
    >{{ 'R$ ' + item.vlTarifaParcelaLoja.toLocaleString("pt-BR", { minimumFractionDigits: 2 }) }}</td>
    <td
      v-if="!!item.cobrarCarneLoja"
      class="text-xs-center">
      <v-icon color="success">mdi-check-bold</v-icon>
    </td>
    <td v-else />
    <td
      class="text-xs-left"
    >{{ item.nomeUsuario }}</td>
    <td
      v-if="permiteExluirOrAlterar(item)"
      class="justify-end layout ma-2">
      <template
        v-if="dialog"
      >
        <v-layout
          row
          justify-center>
          <v-dialog
            v-model="dialog"
            max-width="390">
            <v-card>
              <v-card-title class="headline">Excluir Empresa</v-card-title>

              <v-card-text>Confirma a exclusão da Regra? Essa operação não poderá ser desfeita.</v-card-text>

              <v-card-actions>
                <v-spacer/>

                <v-btn
                  outline
                  style="color: black !important"
                  @click="closeDialogDelete">Cancelar</v-btn>

                <v-btn
                  :loading="loadingBtn"
                  color="red"
                  @click="deleteItem(item)">Excluir</v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-layout>
      </template>
      <v-icon
        class="mr-2"
        color="blue"
        title="Editar dados da empresa"
        @click="alterarRegra(item)"
      >mdi-pencil</v-icon>
      <v-icon
        color="red"
        title="Excluir a empresa"
        @click="openDialogDelete(item)"
      >mdi-delete</v-icon>
    </td>
    <td
      v-else
      class="justify-end layout ma-2">
      <v-icon
        class="mr-2"
        @click="visualizarRegra(item)"
      >mdi-magnify</v-icon>
    </td>
  </tr>
</template>
<script>

import moment from 'moment/src/moment'
import { RegraFinanceiraBusiness } from '../../business'

export default {
  props: {
    item: {
      type: Object,
      required: true
    },
    visualizarRegra: {
      type: Function,
      required: true
    },
    alterarRegra: {
      type: Function,
      required: true
    },
    reload: {
      type: Function,
      required: true
    }
  },
  data () {
    return {
      hoje: moment().format('YYYY-MM-DD'),
      selectedItem: null,
      dialog: false,
      loadingBtn: false
    }
  },
  methods: {
    permiteExluirOrAlterar (item) {
      return !item.isVigente && item.dataVigencia >= this.hoje
    },
    editItem (item) {
      this.selectedItem = item
    },
    openDialogDelete (item) {
      this.selectedItem = item
      this.dialog = true
    },
    closeDialogDelete () {
      this.selectedItem = null
      this.dialog = false
    },
    deleteItem (item) {
      this.loadingBtn = true
      RegraFinanceiraBusiness.delete(Number(item.id))
        .then(response => {
          this.$root.showSucesso(response.data)
          this.loadingBtn = false
          this.dialog = false
          this.reload()
        }).catch(erro => {
          this.$root.showErro(erro.data)
          this.loadingBtn = false
          this.dialog = false
        })
    }
  }
}
</script>
