<template>
  <v-layout
    row
    wrap>
    <core-progress-modal :show="loading" />
    <template v-if="!loading">
      <v-flex md12>
        <v-card class="elevation-0">
          <v-card-title>
            <h3 class="headline mb-0">Regras</h3>
          </v-card-title>
          <v-card-text>
            <v-layout
              row
              wrap>
              <v-flex
                md2
                sm12>
                <v-text-field
                  v-validate="'required'"
                  :disabled="disabled || primeiraRegra"
                  v-model="value.dataVigencia"
                  :error-messages="errors.collect('Data Vigente')"
                  :rules="[rulesDate.validaDataVigencia]"
                  type="date"
                  label="Data Vigente"
                  data-vv-name="Data Vigente"
                  required
                />
              </v-flex>
            </v-layout>
          </v-card-text>
        </v-card>

        <br>

        <v-card class="elevation-0">
          <v-card-title>
            <h3 class="headline mb-0">Regras Financeiras</h3>
          </v-card-title>
          <v-card-text>
            <v-layout
              row
              wrap>
              <v-flex
                md3
                sm6
                xs12>
                <v-text-field
                  v-formata-moeda="value.peMultaPorAtraso"
                  v-money="money"
                  v-validate="'required'"
                  v-model.lazy="value.peMultaPorAtraso"
                  :disabled="disabled"
                  :error-messages="errors.collect('Multa por Atraso')"
                  data-vv-name="Multa por Atraso"
                  type="tel"
                  prefix="%"
                  label="Multa por Atraso"
                  maxlength="5"
                  reverse
                  required
                />
              </v-flex>
              <v-flex
                md3
                sm6
                xs12>
                <v-text-field
                  v-formata-moeda="value.peJurosMoraPorAtraso"
                  v-money="money"
                  v-validate="'required'"
                  v-model="value.peJurosMoraPorAtraso"
                  :disabled="disabled"
                  :error-messages="errors.collect('Juros Mora por Atraso')"
                  data-vv-name="Juros Mora por Atraso"
                  type="tel"
                  prefix="%"
                  label="Juros Mora por Atraso"
                  maxlength="5"
                  reverse
                  required
                />
              </v-flex>
              <!--
                <v-flex
                md3
                sm6
                xs12>
                <v-text-field
                  v-formata-moeda="value.peLimiteGlobal"
                  v-money="money"
                  v-validate="'required'"
                  v-model.lazy="value.peLimiteGlobal"
                  :disabled="disabled"
                  :error-messages="errors.collect('Percentual do Limite Global')"
                  data-vv-name="Percentual do Limite Global"
                  required
                  type="tel"
                  prefix="%"
                  label="Percentual do Limite Global"
                  reverse
                  maxlength="5"
                />
              </v-flex>
              -->
              <v-flex
                md3
                sm6
                xs12>
                <v-text-field
                  v-formata-moeda="value.vlTarifaParcelaBanco"
                  v-money="money"
                  v-validate="'required'"
                  v-model.lazy="value.vlTarifaParcelaBanco"
                  :disabled="disabled"
                  :error-messages="errors.collect('Tarifa por Parcela Banco')"
                  data-vv-name="Tarifa por Parcela Banco"
                  required
                  type="tel"
                  suffix="R$"
                  reverse
                  label="Tarifa por Parcela Banco"
                />
              </v-flex>

              <v-flex
                md3
                sm6
                xs12>
                <v-text-field
                  v-formata-moeda="value.vlTarifaParcelaLoja"
                  v-money="money"
                  v-validate="'required'"
                  v-model.lazy="value.vlTarifaParcelaLoja"
                  :disabled="disabled"
                  :error-messages="errors.collect('Tarifa por Parcela Loja')"
                  data-vv-name="Tarifa por Parcela Loja"
                  required
                  type="tel"
                  suffix="R$"
                  reverse
                  label="Tarifa por Parcela Loja"
                />
              </v-flex>
              <v-flex
                md3
                sm3
                xs12>
                <v-text-field
                  v-formata-moeda="value.peOverLimite"
                  v-money="money"
                  v-validate="'required'"
                  v-model.lazy="value.peOverLimite"
                  :disabled="disabled"
                  :error-messages="errors.collect('Over Limit')"
                  data-vv-name="Over Limit"
                  required
                  type="tel"
                  prefix="%"
                  label="Over Limit"
                  reverse
                  maxlength="5"
                  hint="<span class='body-2 red--text'>percentual a mais do limite liberado para efetuar a venda</span>"
                />
              </v-flex>
              <v-flex
                md6
                sm9
                xs12>
                <v-autocomplete
                  v-model="value.cobrarCarneBancoId"
                  :items="listaCedente"
                  :disabled="disabled"
                  label="Cobrar Carnê no Banco"
                  item-text="descricao"
                  item-value="id"
                  clearable
                />
              </v-flex>
              <v-flex
                md3
                sm6
                xs12>
                <v-checkbox
                  v-model="value.cobrarCarneLoja"
                  :readonly="disabled"
                  label="Cobrar Carnê em Loja"
                />
              </v-flex>
              <v-flex md12>
                <v-autocomplete
                  v-validate="'required'"
                  v-model="value.listPlanoPagamentoId"
                  :items="planosPagamento"
                  :disabled="disabled"
                  :error-messages="errors.collect('Parcelar em até (Plano de Pagamento)')"
                  data-vv-name="Parcelar em até (Plano de Pagamento)"
                  required
                  label="Parcelar em até (Plano de Pagamento)"
                  item-text="descricao"
                  item-value="id"
                  multiple
                  chips
                />
              </v-flex>
            </v-layout>
          </v-card-text>
        </v-card>

        <br>

        <v-card class="elevation-0">
          <v-card-title>
            <h3 class="headline mb-0">Regras Renegociação (Acordo)</h3>
          </v-card-title>
          <v-card-text>
            <v-layout
              row
              wrap>
              <v-flex
                md4
                sm6
                xs12>
                <v-text-field
                  v-formata-moeda="value.peMaxDescontoAtrasoAcordo"
                  v-money="money"
                  v-validate="'required'"
                  v-model.lazy="value.peMaxDescontoAtrasoAcordo"
                  :disabled="disabled"
                  :error-messages="errors.collect('Máximo Desconto Atraso Acordo')"
                  data-vv-name="Máximo Desconto Atraso Acordo"
                  required
                  type="tel"
                  prefix="%"
                  reverse
                  maxlength="5"
                  label="Máximo Desconto Atraso Acordo"
                />
              </v-flex>
              <v-flex
                md4
                sm6
                xs12>
                <v-text-field
                  v-formata-moeda="value.peMaxDescontoAtualizacaoAcordo"
                  v-money="money"
                  v-validate="'required'"
                  v-model.lazy="value.peMaxDescontoAtualizacaoAcordo"
                  :disabled="disabled"
                  :error-messages="errors.collect('Máximo Desconto Atualização Acordo')"
                  data-vv-name="Máximo Desconto Atualização Acordo"
                  required
                  type="tel"
                  prefix="%"
                  reverse
                  maxlength="5"
                  label="Máximo Desconto Atualização Acordo"
                />
              </v-flex>
              <v-flex
                md4
                sm6
                xs12>
                <v-text-field
                  v-validate="'required'"
                  v-model="value.parcelaAcordoEmAte"
                  :disabled="disabled"
                  :error-messages="errors.collect('Parcelar Acordo em Até')"
                  data-vv-name="Parcelar Acordo em Até"
                  required
                  type="number"
                  prefix="x"
                  reverse
                  label="Parcelar Acordo em Até"
                />
              </v-flex>
              <v-flex
                md4
                sm6
                xs12>
                <v-text-field
                  v-formata-moeda="value.vlMinParcelaAcordo"
                  v-money="money"
                  v-validate="'required'"
                  v-model.lazy="value.vlMinParcelaAcordo"
                  :disabled="disabled"
                  :error-messages="errors.collect('Valor Mínimo da Parcela')"
                  data-vv-name="Valor Mínimo da Parcela"
                  required
                  type="tel"
                  suffix="R$"
                  reverse
                  label="Valor Mínimo da Parcela"
                />
              </v-flex>
              <v-flex
                md4
                sm6
                xs12>
                <v-text-field
                  v-validate="'required'"
                  v-model="value.qtdDiasVencimentoEntradaAcordo"
                  :disabled="disabled"
                  :error-messages="errors.collect('Qtd Dias Vencto Entrada Acordo')"
                  data-vv-name="Qtd Dias Vencto Entrada Acordo"
                  required
                  prefix="dias"
                  min="0"
                  type="number"
                  label="Qtd Dias Vencto Entrada Acordo"
                  reverse
                />
              </v-flex>
              <v-flex
                md4
                sm6
                xs12>
                <v-text-field
                  v-validate="'required'"
                  v-model="value.qtdDiasAtrasoCancelaAutoAcordo"
                  :disabled="disabled"
                  :error-messages="errors.collect('Qtd Dias Atraso Cancel. Automático Acordo')"
                  data-vv-name="Qtd Dias Atraso Cancel. Automático Acordo"
                  required
                  prefix="dias"
                  min="0"
                  type="number"
                  reverse
                  label="Qtd Dias Atraso Cancel. Automático Acordo"
                />
              </v-flex>
              <v-flex
                md4
                sm6
                xs12>
                <v-text-field
                  v-validate="'required'"
                  v-model="value.qtdDiasAtrasoMinAcordo"
                  :disabled="disabled"
                  :error-messages="errors.collect('Qtd Dias Atraso Mínimo p/ Acordo')"
                  data-vv-name="Qtd Dias Atraso Mínimo p/ Acordo"
                  required
                  prefix="dias"
                  min="0"
                  type="number"
                  reverse
                  label="Qtd Dias Atraso Mínimo p/ Acordo"
                />
              </v-flex>
              <v-flex
                md4
                sm6
                xs12>
                <v-text-field
                  v-formata-moeda="value.vlTarifaParaAcordo"
                  v-money="money"
                  v-validate="'required'"
                  v-model="value.vlTarifaParaAcordo"
                  :disabled="disabled"
                  :error-messages="errors.collect('Valor Tarifa para Acordo')"
                  data-vv-name="Valor Tarifa para Acordo"
                  required
                  type="tel"
                  suffix="R$"
                  reverse
                  label="Valor Tarifa para Acordo"
                />
              </v-flex>
              <v-flex
                md4
                sm6
                xs12>
                <v-text-field
                  v-formata-moeda="value.taxaJurosParcelaAcordo"
                  v-money="money"
                  v-validate="'required'"
                  v-model="value.taxaJurosParcelaAcordo"
                  :disabled="disabled"
                  :error-messages="errors.collect('Taxa Juros Parcelamento Acordo')"
                  data-vv-name="Taxa Juros Parcelamento Acordo"
                  required
                  type="tel"
                  prefix="%"
                  reverse
                  maxlength="5"
                  label="Taxa Juros Parcelamento Acordo"
                />
              </v-flex>
            </v-layout>
          </v-card-text>
        </v-card>

        <br>

        <v-card class="elevation-0">
          <v-card-title>
            <h3 class="headline mb-0">Regras Autorização</h3>
          </v-card-title>
          <v-card-text>
            <v-layout
              row
              wrap>
              <v-flex md12>
                <v-checkbox
                  v-model="value.flgRealizarConsCadastral"
                  :disabled="disabled"
                  label="Efetuar nova consulta cadastral na autorização de compra"
                />
              </v-flex>
              <v-flex md12>
                <v-text-field
                  v-validate="'required'"
                  v-model="value.tempoDeCadastro"
                  :disabled="disabled"
                  :error-messages="errors.collect('Tempo de cadastro')"
                  data-vv-name="Tempo de cadastro"
                  required
                  prefix="dias"
                  min="0"
                  type="number"
                  label="Tempo de cadastro"
                  reverse
                />
              </v-flex>
              <v-flex
                class="title"
                md12>
                Tempo de cadastro Inferior ao informado acima:
              </v-flex>
              <v-flex md12>
                <v-layout
                  row
                  class="layout align-center ml-2">
                  - Reutilizar as consultas feitas nos últimos
                  <v-text-field
                    v-validate="'required'"
                    v-model="value.qtdDiasValidSPCAutoriz1"
                    :disabled="disabled"
                    :error-messages="errors.collect('dias')"
                    data-vv-name="dias"
                    required
                    prefix="dias"
                    step="1"
                    min="0"
                    type="number"
                    class="text-field-custom"
                    reverse
                  />apenas para os clientes com maior atraso superior a
                  <v-text-field
                    v-validate="'required'"
                    v-model="value.qtdDiasMaiorAtrasoSPCAutoriz1"
                    :disabled="disabled"
                    :error-messages="errors.collect('Maior atraso')"
                    data-vv-name="Maior atraso"
                    required
                    prefix="dias"
                    step="1"
                    min="0"
                    type="number"
                    class="text-field-custom"
                    reverse
                  />
                </v-layout>
              </v-flex>
              <v-flex
                class="title  mt-2"
                md12>
                Tempo de cadastro Superior ao informado acima:
              </v-flex>
              <v-flex md12>
                <v-layout
                  row
                  class="layout align-center ml-2">
                  - Reutilizar as consultas feitas nos últimos
                  <v-text-field
                    v-validate="'required'"
                    v-model="value.qtdDiasValidSPCAutoriz2"
                    :disabled="disabled"
                    :error-messages="errors.collect('dias')"
                    data-vv-name="dias"
                    required
                    prefix="dias"
                    step="1"
                    min="0"
                    type="number"
                    class="text-field-custom"
                    reverse
                  />apenas para os clientes com maior atraso superior a
                  <v-text-field
                    v-validate="'required'"
                    v-model="value.qtdDiasMaiorAtrasoSPCAutoriz2"
                    :disabled="disabled"
                    :error-messages="errors.collect('Atraso superior')"
                    data-vv-name="Atraso superior"
                    required
                    prefix="dias"
                    step="1"
                    min="0"
                    type="number"
                    class="text-field-custom"
                    reverse
                  />
                </v-layout>
              </v-flex>
            </v-layout>
          </v-card-text>
        </v-card>

        <br>

        <v-card class="elevation-0">
          <v-card-title>
            <v-layout column>
              <h3 class="headline mb-0">Regras Entradas e Concessão de Créditos</h3>
              <span
                class="title mt-2"
              >Regras para solicitar entrada de cliente novo ou antigo (*)</span>
            </v-layout>
          </v-card-title>
          <v-card-text>
            <v-layout
              row
              wrap>
              <v-flex
                class="title mt-2"
                md12>
                Cliente Novo:
              </v-flex>
              <v-flex
                class="layout align-center mt-2 ml-2"
                md12>
                - Solicitar entrada mínima de
                <v-text-field
                  v-formata-moeda="value.peEntradaCarneNovos"
                  v-money="money"
                  v-validate="'required'"
                  v-model="value.peEntradaCarneNovos"
                  :disabled="disabled"
                  :error-messages="errors.collect('Entrada Mínima')"
                  data-vv-name="Entrada Mínima"
                  required
                  class="text-field-custom"
                  type="tel"
                  prefix="%"
                  maxlength="5"
                  reverse
                />
                para cliente novo com restrição ou análise visual no SPC / SERASA.
              </v-flex>
              <v-flex
                class="ml-2"
                md12>
                <v-checkbox
                  v-model="value.flgObrigaEntradaNovos"
                  :disabled="disabled"
                  label="Obrigar entrada para cliente novo independente do resultado do SPC / SERASA"
                />
              </v-flex>
              <v-flex
                class="title"
                md12>
                Cliente Antigo:
              </v-flex>
              <v-flex
                class="layout align-center ml-2"
                md12>
                - Considerar cliente antigo aquele que efetuar pelo menos
                <v-text-field
                  v-validate="'required'"
                  v-model="value.qtdMinPagCarneClienteAntigo"
                  :disabled="disabled"
                  :error-messages="errors.collect('Pagamentos')"
                  data-vv-name="Pagamentos"
                  required
                  prefix="pagamentos"
                  min="0"
                  type="number"
                  reverse
                  class="text-field-custom"
                />
              </v-flex>

              <v-flex
                class="layout align-center mt-2 ml-2"
                md12>
                - Solicitar entrada mínima de
                <v-text-field
                  v-formata-moeda="value.peEntradaCarneAntigos"
                  v-money="money"
                  v-validate="'required'"
                  v-model="value.peEntradaCarneAntigos"
                  :disabled="disabled"
                  :error-messages="errors.collect('Entrada mínima')"
                  data-vv-name="Entrada mínima"
                  required
                  class="text-field-custom"
                  type="tel"
                  prefix="%"
                  maxlength="5"
                  reverse
                />
                para cliente antigo com maior atraso superior a
                <v-text-field
                  v-validate="'required'"
                  v-model="value.qtDiasAtrasoEntradaCarneAntigos"
                  :disabled="disabled"
                  :error-messages="errors.collect('dias')"
                  data-vv-name="dias"
                  required
                  class="text-field-custom"
                  prefix="dias"
                  step="1"
                  min="0"
                  type="number"
                  reverse
                />,
              </v-flex>
              <v-flex
                class="layout align-center mt-2 ml-2"
                md12>
                considerando os
                <v-text-field
                  v-validate="'required'"
                  v-model="value.qtdPagAtrasoEntradaCarneAntigos"
                  :disabled="disabled"
                  :error-messages="errors.collect('Últimos Pagamentos')"
                  data-vv-name="Últimos Pagamentos"
                  required
                  type="number"
                  step="1"
                  min="0"
                  class="text-field-custom"
                  reverse
                />
                últimos pagamentos especificados (**)
              </v-flex>
              <v-flex
                class="layout align-center mt-2 ml-2"
                md12>
                - Se última compra há mais de
                <v-text-field
                  v-validate="'required'"
                  v-model="value.qtdMinDiasCompraClienteAntigo"
                  :disabled="disabled"
                  :error-messages="errors.collect('Última Compra')"
                  data-vv-name="Última Compra"
                  required
                  type="number"
                  prefix="dias"
                  step="1"
                  min="0"
                  class="text-field-custom"
                  reverse
                />, refazer a consulta do SPC / SERASA para atribuir uma nova classificação de grupo.
              </v-flex>
              <v-divider />

              <v-flex
                class="title mt-2"
                md12>
                Regras para solicitar entrada segundo critério de grupo de risco considerado na última consulta SCORE do SPC Brasil ou SPCA
              </v-flex>
              <v-flex md12>
                <v-autocomplete
                  v-validate="'required'"
                  v-model="value.servicoSPC"
                  :items="servicoSPCList"
                  :disabled="disabled"
                  :error-messages="errors.collect('motor de crédito')"
                  data-vv-name="motor de crédito"
                  required
                  item-value="id"
                  item-text="descricao"
                  hint="<span class='body-2 red--text'><span class='body-2 red--text'>Selecione qual motor de crédito será utilizado para consulta</span>"
                  persistent-hint
                />
              </v-flex>

              <v-flex
                v-for="grupo in ['A', 'B', 'C', 'D', 'E', 'F', 'Z']"
                :key="grupo"
                md12
              >
                <v-card>
                  <v-card-text>
                    <v-layout
                      row
                      class="layout fill-height align-center justify-start"
                      wrap>
                      <v-flex
                        md4
                        sm6
                        xs12>
                        <span
                          class="title"
                        >Lim. Crédito Grupo {{ grupo === 'Z' ? 'Não Informado' : grupo }}</span>
                      </v-flex>
                      <v-flex
                        md8
                        sm6
                        xs12>
                        <v-checkbox
                          v-model="value['limiteCreditoGrupo'+grupo].flgAnaliseVisual"
                          :disabled="disabled"
                          :persistent-hint="value['limiteCreditoGrupo'+grupo].flgAnaliseVisual"
                          label="Análise Visual"
                          hint="Cliente Análise Visual foge da regra de faixa de concessão, vai para fila de análise manual  e deve ser configurado na mão o limite e o percentual de entrada."
                        />
                      </v-flex>
                      <v-layout
                        v-if="!value['limiteCreditoGrupo'+grupo].flgAnaliseVisual"
                        row
                        class="layout fill-height align-center justify-start"
                        wrap>
                        <v-flex
                          md3
                          sm12
                        >
                          <span class="title">Faixa 1:</span>
                        </v-flex>
                        <v-flex
                          md3
                          sm4
                          xs12>
                          <v-text-field
                            v-formata-moeda="value['limiteCreditoGrupo'+grupo].faixaInicial1"
                            v-money="money"
                            v-model.lazy="value['limiteCreditoGrupo'+grupo].faixaInicial1"
                            :disabled="disabled"
                            :persistent-hint="true"
                            type="tel"
                            suffix="R$"
                            label="Inicial R$"
                            hint="(0 = recusar)"
                            reverse
                          />
                        </v-flex>
                        <v-flex
                          md3
                          sm4
                          xs12>
                          <v-text-field
                            v-formata-moeda="value['limiteCreditoGrupo'+grupo].faixaFinal1"
                            v-money="money"
                            v-model.lazy="value['limiteCreditoGrupo'+grupo].faixaFinal1"
                            :disabled="disabled"
                            :persistent-hint="true"
                            type="tel"
                            suffix="R$"
                            label="Final R$"
                            hint="(0 = recusar)"
                            reverse
                          />
                        </v-flex>
                        <v-flex
                          md3
                          sm4
                          xs12>
                          <v-text-field
                            v-formata-moeda="value['limiteCreditoGrupo'+grupo].peEntrada1"
                            v-money="money"
                            v-model.lazy="value['limiteCreditoGrupo'+grupo].peEntrada1"
                            :disabled="disabled"
                            type="tel"
                            prefix="%"
                            maxlength="5"
                            label="% Entrada"
                            reverse
                          />
                        </v-flex>

                        <v-flex
                          md3
                          sm12>
                          <span class="title">Faixa 2:</span>
                        </v-flex>
                        <v-flex
                          md3
                          sm4
                          xs12>
                          <v-text-field
                            v-formata-moeda="value['limiteCreditoGrupo'+grupo].faixaInicial2"
                            v-money="money"
                            v-model.lazy="value['limiteCreditoGrupo'+grupo].faixaInicial2"
                            :disabled="disabled"
                            :persistent-hint="true"
                            type="tel"
                            suffix="R$"
                            label="Inicial R$"
                            hint="(0 = recusar)"
                            reverse
                          />
                        </v-flex>
                        <v-flex
                          md3
                          sm4
                          xs12>
                          <v-text-field
                            v-formata-moeda="value['limiteCreditoGrupo'+grupo].faixaFinal2"
                            v-money="money"
                            v-model.lazy="value['limiteCreditoGrupo'+grupo].faixaFinal2"
                            :disabled="disabled"
                            :persistent-hint="true"
                            type="tel"
                            suffix="R$"
                            label="Final R$"
                            hint="(0 = recusar)"
                            reverse
                          />
                        </v-flex>
                        <v-flex
                          md3
                          sm4
                          xs12>
                          <v-text-field
                            v-formata-moeda="value['limiteCreditoGrupo'+grupo].peEntrada2"
                            v-money="money"
                            v-model.lazy="value['limiteCreditoGrupo'+grupo].peEntrada2"
                            :disabled="disabled"
                            type="tel"
