                  label="Rotina de aumento de limite de crédito"
                  item-text="descricao"
                  item-value="id"
                />
              </v-flex>
              <template v-if="value.tpRegra === 1">
                <v-flex
                  md12
                  class="title">Condições para o aumento automático do limite</v-flex>
                <v-flex md2>
                  <v-text-field
                    v-model="value.rotinaAtualizaLimCred1"
                    :disabled="disabled"
                    label="Periodicidade Atualização mes(es)"
                    type="number"
                    min="0"
                    prefix="meses"
                    reverse
                  />
