                    v-model="value.qtdFaturasPagas1"
                    :disabled="disabled"
                    label="Quantidade Pagamentos fatura(s)"
                    type="number"
                    min="0"
                    reverse
                  />
                </v-flex>
                <v-flex md2>
                  <v-text-field
                    v-model="value.qtdDiasAtrasoMax1"
                    :disabled="disabled"
                    label="Atraso Máximo Pagamentos dia(s)"
                    type="number"
                    step="1"
                    min="0"
                    prefix="dias"
                    reverse
                  />
