                <v-flex md2>
                  <v-text-field
                    v-money="money"
                    v-formata-moeda="value.peAumentoMes1"
                    v-model="value.peAumentoMes1"
                    :disabled="disabled"
                    prefix="%"
                    label="% Aumento Limite Mês"
                    maxlength="5"
                    type="tel"
                    reverse
                  />
                </v-flex>
                <v-flex md2>
                  <v-text-field
                    v-money="money"
                    v-formata-moeda="value.peAumentoAcm1"
                    v-model="value.peAumentoAcm1"
                    :disabled="disabled"
                    prefix="%"
                    label="% Aumento Limite Global"
                    maxlength="5"
                    type="tel"
                    reverse
                  />
                </v-flex>
                <v-flex md2>
                  <v-text-field
                    v-money="money"
                    v-formata-moeda="value.vlLimMesMaxAumento1"
                    v-model="value.vlLimMesMaxAumento1"
                    :disabled="disabled"
                    suffix="R$"
                    label="Limite Mês Máximo"
                    type="tel"
                    reverse
                  />
                </v-flex>
                <v-flex md2>
                  <v-text-field
                    v-money="money"
                    v-formata-moeda="value.vlLimAcmMaxAumento1"
                    v-model="value.vlLimAcmMaxAumento1"
                    :disabled="disabled"
                    suffix="R$"
                    label="Limite Global Máximo"
                    type="tel"
                    reverse
                  />
                </v-flex>
                <v-flex md2>
                  <v-text-field
                    v-model="value.qtdDiasSemCompraMin1"
                    :disabled="disabled"
                    label="Mínimo de Dias Sem Comprar"
                    type="number"
                    min="0"
                    prefix="dias"
                    reverse
                  />
                </v-flex>
                <v-flex
                  class="layout align-end"
                  md12>
                  Não aumentar limite se cliente com atraso de
                  <v-text-field
                    v-model="value.qtdDiasAtrasoContaMaxLimite1"
                    :disabled="disabled"
                    type="number"
                    class="text-field-custom"
                    step="1"
                    min="0"
                    prefix="dias"
                    reverse
                  /> ou superior. Deixe ZERO caso não queira verificar essa condição.
                </v-flex>
              </template>
              <template v-if="value.tpRegra === 2">
                <v-flex
                  md12
                  class="title">Condições para o aumento automático do limite</v-flex>
                <v-flex md12>
                  <v-layout
                    row
                    class="layout fill-height align-center justify-start">
                    1) Atraso máximo de
                    <v-flex md1>
                      <v-text-field
                        v-model="value.qtdDiasAtrasoMax2"
                        :disabled="disabled"
                        type="number"
                        step="1"
                        min="0"
                        prefix="dias"
                        reverse
                      />
                    </v-flex> no pagamento da fatura
                  </v-layout>
                </v-flex>
                <v-flex md12>
                  <v-layout
                    row
                    class="layout fill-height align-center justify-start">
                    2) Ter pago pelo menos
                    <v-flex md1>
                      <v-text-field
                        v-money="money"
                        v-formata-moeda="value.peMinPagFaturaAumentoLimite2"
                        v-model="value.peMinPagFaturaAumentoLimite2"
                        :disabled="disabled"
                        prefix="%"
                        maxlength="5"
                        type="tel"
                        reverse
                      />
                    </v-flex>da última fatura
                  </v-layout>
                </v-flex>
                <v-flex
                  class="layout align-end"
                  md12>
                  3) Para clientes com limite de crédito maior ou igual a
                  <v-text-field
                    v-money="money"
                    v-formata-moeda="value.vlLimiteMinPeUtilizAumentoLimiteAuto2"
                    v-model="value.vlLimiteMinPeUtilizAumentoLimiteAuto2"
                    :disabled="disabled"
                    class="text-field-custom"
                    suffix="R$"
                    type="tel"
                    reverse
                  />, deve ter sido utilizado no mínimo
                  <v-text-field
                    v-money="money"
                    v-formata-moeda="value.peMinUtilizAumentoLimiteAuto2"
                    v-model="value.peMinUtilizAumentoLimiteAuto2"
                    :disabled="disabled"
                    class="text-field-custom"
                    prefix="%"
                    maxlength="5"
                    type="tel"
                    reverse
                  />do limite de crédito
                </v-flex>
                <v-flex
                  md12
                  class="title"
                >Valor do aumento automático (Uma única vez no ciclo da fatura)</v-flex>
                <v-flex
                  class="layout align-end"
                  md12>
                  1) Aumento de
                  <v-text-field
                    v-money="money"
                    v-formata-moeda="value.vlAumentoLimiteAbaixoVlBaseAumentoLimiteAuto2"
                    v-model="value.vlAumentoLimiteAbaixoVlBaseAumentoLimiteAuto2"
                    :disabled="disabled"
                    class="text-field-custom"
                    suffix="R$"
                    type="tel"
                    reverse
                  />para clientes com limite até
                  <v-text-field
                    v-money="money"
                    v-formata-moeda="value.vlBaseLimCreditoAumentoLimiteAuto2"
                    v-model="value.vlBaseLimCreditoAumentoLimiteAuto2"
                    :disabled="disabled"
                    class="text-field-custom"
                    suffix="R$"
                    type="tel"
                    reverse
                  />e de
                  <v-text-field
                    v-money="money"
                    v-formata-moeda="value.vlAumentoLimiteAcimaVlBaseAumentoLimiteAuto2"
                    v-model="value.vlAumentoLimiteAcimaVlBaseAumentoLimiteAuto2"
                    :disabled="disabled"
                    class="text-field-custom"
                    suffix="R$"
                    type="tel"
                    reverse
                  />para os com limite superior
                </v-flex>
                <v-flex
                  class="layout align-end"
                  md12>
                  2) O limite de crédito não poderá ultrapassar
                  <v-text-field
                    v-money="money"
                    v-formata-moeda="value.vlLimAcmMaxAumento2"
                    v-model="value.vlLimAcmMaxAumento2"
                    :disabled="disabled"
                    class="text-field-custom"
                    suffix="R$"
                    type="tel"
                    reverse
                  />(0 = sem limite máximo)
                </v-flex>
              </template>
            </v-layout>
          </v-card-text>
        </v-card>

        <br>

        <v-card class="elevation-0">
          <v-card-title>
            <h3 class="headline mb-0">Outras Regras</h3>
          </v-card-title>
          <v-card-text>
            <v-layout
              row
              wrap>
              <v-flex md4>
                <v-text-field
                  v-validate="'required'"
                  v-model="value.diasPagtoLoja"
                  :disabled="disabled"
                  :error-messages="errors.collect('Dias pgto em loja')"
                  data-vv-name="Dias pgto em loja"
                  label="Dias pgto em loja"
                  required
                  type="number"
                  min="0"
                  prefix="dias"
                  reverse
                />
              </v-flex>
              <v-flex md4>
                <v-text-field
                  v-validate="'required'"
                  v-model="value.diasPagtoBanco"
                  :disabled="disabled"
                  :error-messages="errors.collect('Dias pgto em banco')"
                  data-vv-name="Dias pgto em banco"
                  label="Dias pgto em banco"
                  required
                  type="number"
                  min="0"
                  prefix="dias"
                  reverse
                />
              </v-flex>
              <v-flex md4>
                <v-text-field
                  v-validate="'required'"
                  v-model="value.diasCarenciaEncargos"
                  :disabled="disabled"
                  :error-messages="errors.collect('Dias carência encargos')"
                  data-vv-name="Dias carência encargos"
                  label="Dias carência encargos"
                  required
                  type="number"
                  min="0"
                  prefix="dias"
                  reverse
                />
              </v-flex>

              <v-flex md4>
                <v-text-field
                  v-model="value.qtdMaxDependentes"
                  :disabled="disabled"
                  label="Qtd máxima de dependentes"
                  type="number"
                  min="0"
                  reverse
                />
              </v-flex>
              <v-flex md4>
                <v-text-field
                  v-validate="'required'"
                  v-model="value.diasAtrasoBloqueioAutorizacao"
                  :disabled="disabled"
                  :error-messages="errors.collect('Dias atraso bloqueio autorização')"
                  data-vv-name="Dias atraso bloqueio autorização"
                  label="Dias atraso bloqueio autorização (0 = sem bloqueio)"
                  required
                  type="number"
                  min="0"
                  prefix="dias"
                  reverse
                />
              </v-flex>
              <v-flex md4>
                <v-text-field
                  v-money="money"
                  v-formata-moeda="value.vlAtrasoLimiteAutorizacao"
                  v-validate="'required'"
                  v-model="value.vlAtrasoLimiteAutorizacao"
                  :disabled="disabled"
                  :error-messages="errors.collect('Valor atraso limite')"
                  data-vv-name="Valor atraso limite"
                  required
                  suffix="R$"
                  label="Valor atraso limite p/ autorização (0 = sem limite)"
                  type="tel"
                  reverse
                />
              </v-flex>

              <v-flex md3>
                <v-checkbox
                  v-model="value.flgEmiteCartaoBloqueado"
                  :disabled="disabled"
                  label="Emite cartão bloqueado"
                />
              </v-flex>
              <v-flex md3>
                <v-checkbox
                  v-model="value.flgLiberarComprasClientesSPC"
                  :disabled="disabled"
                  label="Liberar compras clientes SPC/SERASA?"
                />
              </v-flex>
              <v-flex md4>
                <v-checkbox
                  v-model="value.flgLiberarComprasClientesSPCOutrasRedes"
                  :disabled="disabled"
                  label="Liberar compras clientes SPC/SERASA outras redes?"
                />
              </v-flex>
              <v-flex md2>
                <v-autocomplete
                  v-model="value.embocadoraId"
                  :items="embocadoras"
                  :disabled="disabled"
                  label="Emboçadora"
                  item-text="descricao"
                  item-value="id"
                />
              </v-flex>

              <v-flex md12>
                <v-textarea
                  v-model="value.msgPadraoSms"
                  :disabled="disabled"
                  label="Mensagem padrão para SMS"
                />
              </v-flex>
            </v-layout>
          </v-card-text>
        </v-card>

        <br>

        <v-card class="elevation-0">
          <v-card-title>
            <h3 class="headline mb-0">Mensagens Automáticas</h3>
            <p>
              &nbsp;&nbsp;&nbsp;&nbsp;Você pode personalizar o
              <b>SMS/WhatsApp</b> colocando no texto as variáveis
              <b>#NM#</b> (primeiro nome do cliente),
              <b>#CODE#</b> (código) e/ou as a seguir referentes a última fatura fechada:
              <b>#VL#</b> (valor),
              <b>#DT#</b> (vencimento),
              <b>#VP#</b> (valor da parcela),
              <b>#PD#</b> (nome do produto),
              <b>#NL#</b> (nome da loja).
            </p>
          </v-card-title>
          <v-card-text>
            <v-layout
              row
              wrap>
              <v-flex md12>
                <v-textarea
                  v-model="value.msgBoasVindas"
                  :disabled="disabled"
                  label="Mensagem de boas vindas -- Tags permitidas:#NM#,#NL#"
                />
              </v-flex>
              <v-flex md12>
                <v-textarea
                  v-model="value.msgAutorizaCompra"
                  :disabled="disabled"
                  label="Mensagem de autorização de compra -- Tags permitidas:#NM#,#NL#,#CODE#,#VL#,#PD#"
                />
              </v-flex>
              <v-flex md12>
                <v-textarea
                  v-model="value.msgCompraEfetuada"
                  :disabled="disabled"
                  label="Mensagem de compra efetuada -- Tags permitidas:#NM#,#NL#,#VL#,#DT#,#VP#"
                />
              </v-flex>
              <v-flex md12>
                <v-textarea
                  v-model="value.msgAumentoCredito"
                  :disabled="disabled"
                  label="Mensagem de aumento de crédito -- Tags permitidas:#NM#,#VL#"
                />
              </v-flex>
            </v-layout>
          </v-card-text>
        </v-card>
      </v-flex>
    </template>
    <v-layout class="justify-end">
      <template v-if="acao === 'ver-regra'">
        <v-btn
          dark
          @click="voltar">Voltar</v-btn>
      </template>
      <template v-else>
        <v-btn
          outline
          style="color: black !important"
          @click="cancelar">Cancelar</v-btn>
        <v-btn
          :loading="loadingBtn"
          color="primary"
          @click="valida"
        >Salvar</v-btn>
      </template>
    </v-layout>
  </v-layout>
</template>

<script>
import { TiposBusiness, BeneficiarioBusiness } from '../../business'
import { MONEY } from '../../constants'
import dateUtils from '../../utils/dateUtils'

export default {
  // https://zaengle.com/blog/using-v-model-on-nested-vue-components
  inject: ['$validator'],
  props: {
    value: {
      type: Object,
      required: true
    },
    salvar: {
      type: Function,
      required: true
    },
    voltar: {
      type: Function,
      required: true
    },
    cancelar: {
      type: Function,
      required: true
    },
    acao: {
      type: String,
      required: true
    },
    primeiraRegra: {
      type: Boolean,
      required: true
    },
    // eslint-disable-next-line vue/require-default-prop
    disabled: false
  },
  data () {
    return {
      money: MONEY,
      listaCedente: [],
      tiposLancamentos: [],
      regrasAumentoLimiteCredito: [
        {
          id: 0,
          descricao: 'Não utilizar rotina de aumento automático de limite'
        },
        { id: 1, descricao: 'Regra 1' },
        { id: 2, descricao: 'Regra 2' }
      ],
      embocadoras: [],
      concessoesCredito: [
        { id: 0, descricao: '' },
        { id: 1, descricao: '1 Cartão Bandeira' },
        { id: 2, descricao: '2 Percentual Renda' },
        { id: 3, descricao: '3 Credit Score' },
        { id: 4, descricao: '4 Grupo de Risco' },
        { id: 5, descricao: '5 Limite Neurotech' },
        { id: 9, descricao: '9 Manual' }
      ],
      planosPagamento: [],
      servicoSPCList: [],
      loading: false,
      loadingBtn: false,
      rulesDate: {
        validaDataVigencia: value => {
          return dateUtils.validaDataVigencia(value)
        }
      }
    }
  },
  watch: {
    value () {
      this.$emit('input', this.value)
    }
  },
  beforeMount () {
    this.loading = true
    // se for a primeira regra deve setar a data atual
    if (this.primeiraRegra) {
      this.value.dataVigencia = this.$moment().format('YYYY-MM-DD')
    }
    TiposBusiness.listPlanoPagamento()
      .then(response => { this.planosPagamento = response.data })
      .catch(() => { this.$root.showErro('Não foi possível buscar as informações dos planos de pagamentos') })
      .finally(() => {
        BeneficiarioBusiness.findAllBanco()
          .then(response => {
            this.listaCedente = response.data
          }).catch(erro => {
            this.$root.showErro(erro.data)
          }).finally(() => {
            TiposBusiness.tiposLancamentoRepasse()
              .then(response => { this.tiposLancamentos = response.data })
              .catch(() => { this.$root.showErro('Não foi possível buscar as informações da lista de Tipos Lançamento') })
              .finally(() => {
                TiposBusiness.listEmbocadora()
                  .then(response => { this.embocadoras = response.data })
                  .catch(() => { this.$root.showErro('Não foi possível buscar as informações das Emboçadoras') })
                  .finally(() => {
                    TiposBusiness.getAllServicoSPC()
                      .then(response => { this.servicoSPCList = response.data })
                      .catch(() => { this.$root.showErro('Não foi possível buscar as informações dos motores de créditos') })
                      .finally(() => {
                        this.loading = false
                      })
                  })
              })
          })
      })
  },
  methods: {
    valida () {
      this.$validator.validate()
        .then(result => {
          if (result) {
            this.salvar()
          } else {
            this.$root.showAlerta(this.$vuetify.t('$vuetify.Erro.camposObrigatorios'))
          }
        })
    }
  }
}
</script>
<style scoped>
  .text-field-custom {
    padding: 0px;
    margin: 0 5px 0 5px;
    height: 30px;
  }
</style>
